
<template >
    <section>
        <div class="col-md-12 p-0 mt-2">
            <div class="theme-primary partition-full">
                <span class="p-name text-white">Timesheet</span>
                <span class="p-actions float-right">
                    <a id="AddManually" href="javascript:;"
                        class="p-action-btn text-white addman btn-info"><span>Expert</span></a>
                    <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info"><span>This
                            week</span></a>
                    <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info"><i
                            class="fa fa-delete mx-2"></i></a>
                </span>
            </div>
        </div>
        <div class="listing py-3 px-3">
            <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons1" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-slotbutton="{ data }">
                    <button class="btn btn-success" @click="FetchData">{{ $t('ShowTimeSheet') }}</button>
                </template>
                <!-- <template v-slot:slotbutton="{data}">
                    <div class="mr-2 d-inline-block">
                        <span class="legends_low colorboxsquare"></span>Manual
                    </div>
                    <button id="btnCancel" title="Cancel" class="btn btn-danger d-none"><i class="fa fa-close pr-2"></i><span>Cancel</span></button> 
                    </template> -->
            </dynamic-form>
            <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="ListSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-TimeSheetTable="{ data }">
                    <div class="listing">
                        <div class="col-12 mb-2">
                            <div class="mr-2 d-inline-block">
                                <span class="legends-purple colorboxsquare"></span>Automatic
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="legends_low colorboxsquare"></span>Manual
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="legends_medium colorboxsquare"></span>TimeSheet
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="legends_profile colorboxsquare"></span>Leave
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="legends_high colorboxsquare"></span>Holiday
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="lg-pausedresumed colorboxsquare"></span>PausedResumed
                            </div>
                        </div>
                    </div>
                    <tg-list :RenderRowActionMethod="renderActions" IdentityColumn="ProjectId" ModuleName="TimeTrack"
                        :HeaderText="$t('Brand')" SubModuleCode="Timesheet" :LegendArray="LegendArray" :SortExp="SortExp"
                        :SortBy="SortBy" :IsShowAction="true" :ListData="TimesheetList" :HeaderData="Headers"
                        :showCheckBox="true" :NorecordfoundText="$t('NoRecordfound')" :HeaderButtons="listheaderbuttons"
                        v-on:HeaderButtonClick="commonHeaderButtonClick">
                        <template v-slot:slotdata="{ data }">
                            <!-- <template v-if="data.column.COLUMN_NAME == 'break_tt'">
                                <input v-model="item.break_tt">
                            </template> -->
                            <template v-if="data.column.COLUMN_NAME == 'ProjectName'">
                                <div class="d-inline-block">
                                    <a title="" value="-1" style="cursor:default;" class="text-dark" href="#">
                                        <span class="d-block font-14 font-weight-medium">{{
                                            data.projectName }}:{{ data.TaskDetail }}</span>
                                        <span class="text-dark d-block font-12">{{
                                            data.trackDescription }}</span>
                                    </a>
                                </div>
                            </template>

                            <template>
                                <input v-model="data.row[data.column.COLUMN_NAME]">
                            </template>

                            <!-- <template v-for="(item, index) in items" :key="index">
                                <template v-if="data.column.COLUMN_NAME === 'ProjectName'">
                                    <input v-model="item.ProjectName">
                                </template>
                            </template> -->

                        </template>
                    </tg-list>
                </template>
            </dynamic-form>



            <!-- <a id="btnCancel" title="Cancel" class="btn btn-danger d-none"><i class="fa fa-close pr-2"></i><span>Cancel</span></a>
        <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" onclick="" name="" title='Copy to other Users'><i class="fa fa-copy pr-2"></i>Copy to other Users</a>
                        <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" onclick="" name="" title='Copy Last week'><i class="fa fa-copy pr-2"></i>Copy Last week</a>
                        <a id="btnSaveAsDraft" isapprover="0" class="btn btn-warning formbtn text-dark mr-2" onclick="" name="" title='SaveAsDraft'><i class="fa fa-save pr-2"></i>SaveAsDraft</a>
                        <a id="btnSendForApproval" isapprover="0" class="btn btn-success text-white formbtn mr-2" onclick="" title='SendForApproval'><i class="fa fa-send pr-2"></i>SendForApproval</a> -->
        </div>
        <!-- <loader :is-visible="isLoading"></loader> -->


    </section>
</template>
<script>
import DataService from '../../services/DataService';
import { getPreviousDay } from "@/assets/common/vue-common.js"
export default
    {
        data() {
            return {
                getPreviousDay,
                FormSchema: [
                    {
                        layoutType: "triple",
                        Data: [
                            {
                                astype: "DateField",
                                label: "Date Range",
                                name: "date_range",
                                value: (new Date()),
                                placeholder: "This week",
                                //validationRules: "required",
                                config: {
                                    mode: "dateTime",
                                    is24hr: true,
                                    format: "YYYY-MM-DD - hh:mm",
                                    minDate: (new Date())
                                    //maxDate: ....
                                }
                            },
                            {
                                astype: "SelectField",
                                label: "User",
                                name: "user_id",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                },
                                validationRules: {
                                    //"required": true
                                },
                            },
                            {
                                astype: "SlotField",
                                label: "",
                                name: "slotbutton",
                                value: "",
                                placeholder: "",

                            },
                        ]
                    }
                ],
                // buttons1: [
                //     {
                //         type: "submit",
                //         class: "btn btn-success",
                //         text: "<i class='fa pr-2'></i>Show Time Sheet",
                //         isVisible: true,
                //     }
                // ],
                ListSchema: [
                    {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "SlotField",
                                label: "",
                                name: "TimeSheetTable",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                },
                                validationRules: {
                                    //"required": true
                                },
                            },

                        ]
                    }
                ],
                buttons: [
                    {
                        type: "submit",
                        class: "btn btn-warning formbtn mr-2 text-dark",
                        text: "<i class='fa fa-copy pr-2'></i>Copy Last Week",
                        isVisible: true,
                    },
                    {
                        type: "submit",
                        class: "btn btn-warning formbtn mr-2 text-dark",
                        text: "<i class='fa fa-save pr-2'></i>Save As Draft",
                        isVisible: true,
                    },
                    {
                        type: "submit",
                        class: "btn btn-success text-white formbtn mr-2",
                        text: "<i class='fa fa-save pr-2'></i>Send For Approval",
                        isVisible: true,
                    }
                ],
                LegendArray: [{
                    class: '',
                    backgroundColor: 'rgb(245 234 222)',
                    name: 'Recurring Tickets'
                }, {
                    class: '',
                    backgroundColor: 'rgb(245 234 222)',
                    name: 'Recurring Tickets'
                }
                ],
                DataLoaded: 0,
                PageNumber: 1,
                PageSize: 10,
                SortBy: '',
                SortExp: '',
                noRecord: false,
                TotalRecords: 0,
                isLoading: false,
                TimesheetList: [],
                UserId: null,
                Headers: [
                        {
                        "COLUMN_NAME": "ProjectName",
                        "DATA_TYPE": "string",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Projects",
                        "DISPLAY_ORDER": 1
                    },
                    // {
                    //     "COLUMN_NAME": "break_tt",
                    //     "DATA_TYPE": "text",
                    //     "SORTABLE": false,
                    //     "VISIBLE": true,
                    //     "DISPLAY_NAME": "Mon, Dec",
                    //     "DISPLAY_ORDER": 2,
                    // },
                ],
            }
        },
        created: async function () {
            this.DataLoaded = 1;
            await this.GetUsersForTimesheet();
            var VueObj = this;
            VueObj.FormSchema[0].Data[1].value = VueObj.GetUserInfo.ID;
            VueObj.FetchData();
        },
        mounted: function () {
            var VueObj = this;
            console.log('mounted')
        },
        methods: {
            async GetUsersForTimesheet() {
                var vm = this;
                vm.isLoading = true;
                var url = `companyId=270936&loggedUserId=769083&userType=CA&userId=769083&moduleName=`;
                await DataService.GetUsersForTimesheet(url)
                    .then(function (response) {

                        response.data.result.forEach(item => {
                            vm.obj = {
                                name: item.username,
                                value: item.useR_ID
                            }
                            vm.FormSchema[0].Data[1].config.options.push(vm.obj);
                        });

                        vm.isLoading = false;
                    });
            },
            OnChange: function () {
                var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
                if (checkCount > 0) {
                    $('#DeleteMultiple').removeClass('disabled');
                }
                else {
                    $('#DeleteMultiple').addClass('disabled');
                }
            },
            commonHeaderButtonClick: function (event) {

                switch (event.callbackfunction) {
                    case "openaddTicket":
                        this.openaddTicket();
                        break;
                    case "setTourGuideSteps":
                        this.setTourGuideSteps(0);
                        break;
                    case "ticketAssigned":
                        this.TicketAssigned();
                        break;
                    case "commonDeleteData":
                        this.commonDeleteData();
                        break;
                    case "openupdateStatus":
                        this.openupdateStatus();
                        //this.showUpdateStatusModal = true;
                        break;
                    case "DownloadExcelDataNew":
                        this.DownloadExcelDataNew();
                        break;
                    case "openFilterPopUp":
                        this.openFilterPopUp();
                        break;
                    case "opentags":
                        this.AssignedTag();
                        breaks;
                    case "removeFilter":
                        break;
                    case 'ManageView':
                        this.ManageView();
                        break;

                }
            },
            actionButtonClick: function (callbackfunction, item, index, event) {
                switch (callbackfunction) {
                    case 'Edit':
                        break;
                    case 'OpenFileModal':
                        this.OpenFileModal(item);
                        break;
                    case 'ManageFollowUp':
                        this.ManageFollowUp(item.ticket_id, item);
                        break;
                    case 'openAddNewEvent':
                        this.openAddNewEvent(item.ticket_id, item);
                        break;
                    case 'openQuickViewpopup':
                        this.openQuickViewpopup(item.ticket_id, item.str_ticket_id, item.ticket_category_id, item.is_field_service);
                        break;
                    case 'openViewRecurringTicket':
                        this.openViewRecurringTicket(item);
                        break;
                    case 'AcceptRejectTicket':
                        this.AcceptRejectTicket(item, event.currentTarget.innerText);
                        break;
                    case 'openViewTicketTab':
                        this.openViewTicketTab(item.ticket_id);
                        break;
                    case 'MarkAsActive':
                        this.MarkAsActive(item.ticket_id);
                    default: this.De
                }
            },
            async onSubmit(formfields, e) {
                var vm = this;
                //this.ListTimesheetForWeek(formfields);
                vm.FetchData();

            },
            renderActions: function (row) {
                var self = this;
            },
            FetchData: async function () {
                var VueObj = this;
                VueObj.isLoading = true;
                var fDate = VueObj.$options.filters.formatDateTime(VueObj.FormSchema[0].Data[0].value)
                var tDate = VueObj.$options.filters.formatDateTime(VueObj.FormSchema[0].Data[0].value)
                // var url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&userId=${VueObj.FormSchema[0].Data[1].value}&companyId=270936`;
                var url = `companyId=270936&userId=${VueObj.FormSchema[0].Data[1].value}&dateFrom=${fDate}&dateTo=${tDate}`;
                await DataService.ListTimesheetForWeek(url).then(response => {
                    if (response.data != null && response.data != "" && response.data.length > 0) {
                        // const headerList=Object.keys(response.data[0]).filter(key => key !== 'ProjectId' && key !== 'ProjectName' && key !== 'TaskId' && key !== 'TaskDetail');

                        const headerList = Object.keys(response.data[0]);

                        VueObj.isLoading = false;
                        // VueObj.Headers.forEach((column) => {
                        //     column.settings = null;
                        //     headerList.forEach(item => {

                        //         if (column.COLUMN_NAME == item) {
                        //             column.settings = {
                        //                 isInSlot: true
                        //             }
                        //         }
                        //         if (column.COLUMN_NAME == 'ProjectName') {
                        //             column.settings = {
                        //                 isInSlot: true
                        //             }
                        //         }
                        //     }
                        //     );
                        // });
                        var count;
                        headerList.forEach(item => {
                            count++;

                            if (item == "ProjectId" || item == "TaskId") {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "string",
                                    "SORTABLE": false,
                                    "VISIBLE": false,
                                    "DISPLAY_NAME": item,
                                    "DISPLAY_ORDER": count,
                                });
                            }
                            else if (item == "TaskDetail") {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "string",
                                    "SORTABLE": false,
                                    "VISIBLE": true,
                                    "DISPLAY_NAME": item,
                                    "DISPLAY_ORDER": count,
                                });
                            }
                            else if (item == "ProjectName")
                            {

                            }
                            else {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "text",
                                    "SORTABLE": false,
                                    "VISIBLE": true,
                                    "DISPLAY_NAME": getPreviousDay(item),
                                    "DISPLAY_ORDER": count,
                                });
                            }
                        });

                        // headerList.forEach((item) => {
                        //     VueObj.Headers.forEach(column => {
                        //         column.settings = null;

                        //         if (column.COLUMN_NAME == item && column.COLUMN_NAME !=='ProjectName' && item !=='ProjectName') {
                        //             column.settings = {
                        //                 //isInSlot: true
                        //             }
                        //         }
                        //     }
                        //     );
                        // });


                        VueObj.Headers.forEach((column) => {
                            column.settings = null;
                            headerList.forEach(item => {

                                if (column.COLUMN_NAME == item && column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                            }
                            );
                        });


                        VueObj.TotalRecords = response.data.length;
                        VueObj.TimesheetList = response.data;
                        setTimeout(function () { vueObj.CheckBoxBootstrap() }, 100);
                        setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);

                        VueObj.TotalPages = 1;
                        VueObj.CurrentPage = 1;
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;

                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;
                    }
                    else {
                        VueObj.noRecord = true;
                        VueObj.TimesheetList = [];
                    }

                });
            }
        }
    }
</script>
